import axios from "axios";
axios.defaults.baseURL = "https://www.easy-mock.com/mock/5c2dbaee32924755e4c0dc04/vue-travel";
class HTTP {
    request({ url, method = "get", data = {}, params = {} }) {
        return axios({
            url,
            method,
            data,
            params
        })
    }
}
export { HTTP }