function getCity() {
    var defaultCity = "武汉";
    try {
        if (localStorage.getItem("city")) {
            defaultCity = localStorage.getItem("city");
        }
    } catch (err) {
        console.log(err);
    }
    return defaultCity
}
function toTop(){
    return 1
}
export default {
    letter:"",
    city: getCity(),
}